<template>
  <!--  搜索结果页面 -->
  <div class="result-cotainer">
    <div class="title-wrap" style=" margin-bottom: 30px;">
      <h2 class="title" >{{$route.query.q}}</h2>
      <span class="sub-title">{{count}}</span>
    </div>
    <el-tabs v-model="activeIndex">
      <!-- 歌曲 -->
      <el-tab-pane label="歌曲" name="songs">
        <table class="el-table">
          <thead>
            <th></th>
            <th>音乐标题</th>
            <th>歌手</th>
            <th>专辑</th>
            <th>时长</th>
          </thead>
          <tbody>
            <tr class="el-table__row" v-for="(item,index) in songs" :key="index" @click="playMusic(item.id)"> 
              <td>{{index+1}}</td>
              <!-- 对应音乐标题 -->
              <td>
                <div class="song-wrng">
                  <div class="name-wrap">
                    <!-- 歌名 -->
                    <span>{{item.name}}</span>
                    <!-- mv图标 -->
                    <span v-if="item.mvid !== 0" class="iconfont -icon-mv"></span>
                    <!-- 二级标题 -->
                    <span v-if="item.alias.length !==0">{{item.alias[0]}}</span>
                  </div>
                </div>
              </td>
              <!-- 对应歌手名 -->
              <td>{{item.artists[0].name}}</td>
              <!-- 专辑名字 -->
              <td>{{item.album.name}}</td>
              <!-- 时长 -->
              <td>{{item.duration}}</td>
            </tr>
          </tbody>
        </table>
      </el-tab-pane>
      <!-- 歌单 -->
      <el-tab-pane label="歌单" name="lists">
        <div class="items">
          <div class="item" @click="toPlaylist(item.id)" v-for="(item, index) in playlists" :key="index" >
            <div class="img-wrap">
              <div class="num-wrap">
                播放量:
                <span class="num">{{item.playCount}}</span>
              </div>
              <!-- 封面图 -->
              <img :src="item.coverImgUrl" alt="">
              <span class="iconfont icon-play"></span>
            </div>
            <!-- 名字 -->
            <p class="name">{{item.name}}</p>
          </div>
        </div>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane label="MV" name="mv">
        <div class="items mv">
          <div class="item" v-for="(item, index) in mvs" :key="index" @click="toMv(item.id)">
            <div class="img-wrap">
              <!-- 封面 -->
              <img :src="item.cover" alt="">
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <!-- 播放次数 -->
                <div class="num">{{item.playCount}}</div>
              </div>
              <!-- 时长 -->
              <div class="time">{{item.duration}}</div>
            </div>
            <div class="info-wrap">
              <!-- mv名 -->
              <span class="name">{{item.name}}</span>
              <!-- 歌手名 -->
              <span class="singer">{{item.artistName}}</span>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 总条数
      total: 0,
      // 页码
      pageNum: 1,
      pageSize: 10,
      keywords: '',
      //tab切换时,会改变的数据
      activeIndex: 'songs',
      //保存 查询歌曲
      songs: [],
      //搜索结果的总数
      count: 0,
      // 保存歌单的字段
      playlists: [],
      //保存的mv字段
      mvs: [],
      songId: 0,
      type: 1
    }
  },
  created () {
    this.FFaxios({
      url: 'https://autumnfish.cn/search',
      method: 'get',
      params: {
        keywords: this.$route.query.q,
        type: 1,
        // 获取的数据量
        limit: 10
      }
    }).then(res => {
      console.log(res)
      this.songs = res.data.result.songs
      // 计算歌曲时间
      for (let i = 0; i < this.songs.length; i++) {
        let min = parseInt(this.songs[i].duration / 1000 / 60)
        let sec = parseInt((this.songs[i].duration / 1000) % 60)
        if (min < 10) {
          min = '0' + min
        }
        if (sec < 10) {
          sec = '0' + sec
        }
        // console.log(min + '|' + sec)
        this.songs[i].duration = min + ':' + sec
      }
      // 保存总数
      this.count = res.data.result.songCount
    })
  },
  watch: {
    activeIndex () {
      // songs 歌曲
      // lists 歌单
      // mv    mv
      let type = 1

      // 获取个数
      let limit = 10
      switch (this.activeIndex) {
        case 'songs':
          type = 1
          limit = 10
          break
        case 'lists':
          type = 1000
          limit = 10
          break
        case 'mv':
          type = 1004
          limit = 8
          break

        default:
          break
      }
      this.FFaxios({
        url: 'https://autumnfish.cn/search',
        method: 'get',
        params: {
          keywords: this.$route.query.q,
          type, // type:type,
          // 获取的数据量
          limit // limit: limit
        }
      }).then(res => {
        console.log(res)
        // 获取歌曲
        if (type == 1) {
          // 歌曲
          this.songs = res.data.result.songs
          // 计算歌曲时间
          for (let i = 0; i < this.songs.length; i++) {
            let min = parseInt(this.songs[i].duration / 1000 / 60)
            let sec = parseInt((this.songs[i].duration / 1000) % 60)
            if (min < 10) {
              min = '0' + min
            }
            if (sec < 10) {
              sec = '0' + sec
            }
            // console.log(min + '|' + sec)
            this.songs[i].duration = min + ':' + sec
          }
          // 保存总数
          this.count = res.data.result.songCount
        }
        // 获取 歌单
        else if (type == 1000) {
          // 歌单的逻辑
          this.playlists = res.data.result.playlists
          // 总数
          this.count = res.data.result.playlistCount
          // 处理 播放次数
          for (let i = 0; i < this.playlists.length; i++) {
            if (this.playlists[i].playCount > 100000) {
              this.playlists[i].playCount =
                parseInt(this.playlists[i].playCount / 10000) + '万'
            }
          }
        } else {
          // 保存mv数据
          this.mvs = res.data.result.mvs
          // 总数
          this.count = res.data.result.mvCount
          // 处理数据
          for (let i = 0; i < this.mvs.length; i++) {
            // 时间
            let min = parseInt(this.mvs[i].duration / 1000 / 60)
            let sec = parseInt((this.mvs[i].duration / 1000) % 60)
            if (min < 10) {
              min = '0' + min
            }
            if (sec < 10) {
              sec = '0' + sec
            }
            this.mvs[i].duration = min + ':' + sec

            // 播放次数
            if (this.mvs[i].playCount > 100000) {
              this.mvs[i].playCount =
                parseInt(this.mvs[i].playCount / 10000) + '万'
            }
          }
        }
      })
    }
  },
  methods: {
    playMusic (id) {
      this.FFaxios({
        url: 'https://autumnfish.cn/song/url',
        method: 'get',
        params: {
          id // id:id
        }
      }).then(res => {
        // console.log(res)
        let url = res.data.data[0].url
        // console.log(this.$parent.musicUrl)
        // 设置给父组件的 播放地址
        this.$parent.musicUrl = url
      })
    }

  }
}
</script>
<style lang='scss'>
.result-cotainer {
  .title-wrap {
    display: flex;
    align-items: flex-end;
    .title {
      margin-right: 10px;
    }
    .sub-title {
      font-size: 15px;
      color: #bebebe;
    }
  }
  .items {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    .item {
      width: 200px;
      cursor: pointer;
      margin-right: 20px;
      margin-bottom: 20px;
      .img-wrap {
        width: 100%;
        position: relative;
        &:hover {
          .icon-play {
            opacity: 1;
          }
        }
        .num-wrap {
          position: absolute;
          color: white;
          top: 0;
          right: 0;
          display: flex;
          align-items: center;
          font-size: 15px;
          padding-right: 5px;
          padding-top: 2px;
          .icon-play {
            font-size: 12px;
            display: flex;
            align-items: center;
            margin-right: 5px;
          }
        }
        > .icon-play {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 40px;
          height: 40px;
          color: #dd6d60;
          font-size: 20px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          background: rgba(255, 255, 255, 0.8);
          opacity: 0;
          &::before {
            transform: translateX(3px);
          }
        }
        img {
          width: 100%;
          height: 200px;
          border-radius: 5px;
        }
        .time {
          position: absolute;
          bottom: 5px;
          right: 5px;
          color: white;
          font-size: 15px;
        }
        .name {
          font-size: 15px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          margin-right: 5px;
        }
        .singer {
          font-size: 14px;
          color: #c5c5c5;
        }
      }
    }
  }
  .mv {
    .item {
      width: 250px;
      margin-left: 0px;
      .img-wrap {
        img {
          height: 150px;
        }
      }
    }
  }
  .el-tabs__item {
    font-size: 18px;
  }
  .el-tabs__header {
    margin: 0 0 15px;
  }
  .el-table td,
  .el-table th.is-leaf {
    border-bottom: none;
  }
}
</style>